<template>
    <div class="box">
        <Head>
            <img src="../../images/110.png" alt="" slot="left" @click="aaa"/>
        </Head>
        <div class="content">
            <input type="text" v-model="val">
            <button @click="add">搜索</button>
        </div>
              <van-notice-bar
                color="#1989fa"
                background="#ecf9ff"
                left-icon="info-o"
                >
                我想告诉大家，我想说，王显赫，属实是太帅了，帅到没有天理了
                </van-notice-bar>
        <div class="address">
            <ul v-for="item in datas">
                <router-link tag="li" :to="{name:'Address',query:{name:item.name}}">
                   {{item.name}}
                </router-link>
                <li>{{item.address}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
import { NoticeBar } from 'vant';
import axios from 'axios'
import Head from  '../Head/Header.vue'
export default {
    components:{
        Head
    },
    methods:{
        aaa(){
            this.$router.go(-1)
        },
        add(){
            if(this.val){
                axios.get('https://elm.cangdu.org/v1/pois?type=nearby&keyword=a'+this.val)
                .then(res=>{
                    console.log(res)
                    this.datas = res.data
                })
            }
        },
        iteNa(){
            this.$router.push({name:'Address',query:{name:item.name}})
        }
    },
    data(){
        return{
            val:'',
            datas:[]
        }
    }

}
</script>

<style lang="less" scoped>
.box{
      width: 100%;
      header{
        width: 100%;
        height: 0.8rem;
        background: #3792E5;
        position: relative;
        img{
            position: absolute;
            top: 0.3rem;
            left: 0.2rem;
            width: 0.3rem;
        }
      }
      .content{
          width: 100%;
          position: relative;
          input{
              width: 5rem;
              height: 0.6rem;
              position: absolute;
              top: 1rem;
              left: 0.3rem;
              background: rgb(197, 190, 190);
              font-size: 0.4rem;
              text-indent: 0.1rem;
          }
          button{
              width: 1.5rem;
              height: 0.67rem;
              background: #3792E5;
              position: absolute;
              border: none;
              font-size: 0.35rem;
              color: white;
              right: 0.3rem;
              top: 1rem;
          }
      }
      .address{
          width: 100%;
          margin-top: 1.1rem;
          ul{
              list-style: none;
              margin-left: 0.3rem;
              margin-top: 0.5rem;
              li{
                  font-size: 0.4rem;
              }
          }
      }
}
</style>


